//todo 1. import store
import store from './store';
import { saveCart } from '../api/cart';

//todo 2. buat variabel tanpa nilai awal
let currentAuth;
let currentCart;

//todo 3. buat fungsi listener
function listener() {
  //todo 4. buat variabel previousAuth dan berikan currentAuth sebagai nilai
  let previousAuth = currentAuth;
  let previousCart = currentCart;

  //todo 5. update nilai dari currentAuth dari nilai state terbaru
  currentAuth = store.getState().auth;
  currentCart = store.getState().cart;

  let { token } = currentAuth;
  //todo 6. cek apakah nilai state `auth` berubah dari nilai sebelumnya
  if (currentAuth !== previousAuth) {
    //todo 7. jika berubah maka simpan ke localStorage
    localStorage.setItem('auth', JSON.stringify(currentAuth));
    saveCart(token, currentCart);
  }
  if (currentCart !== previousCart) {
    localStorage.setItem('cart', JSON.stringify(currentCart));
    saveCart(token, currentCart);
  }
}

//todo 8. buat fungsi listen
function listen() {
  store.subscribe(listener);
}

//todo 9. export listen
export { listen };
