//todo 1. import constants
import { USER_LOGIN, USER_LOGOUT } from './constants';

//todo 2. action untuk userLogin
export function userLogin(user, token) {
  return {
    type: USER_LOGIN,
    user,
    token
  };
}

//todo 3. action untuk userLogout
export function userLogout() {
  return {
    type: USER_LOGOUT
  };
}
