import * as React from 'react';
import TopBar from '../../components/TopBar';
import { LayoutOne, Text, Card, Responsive } from 'upkit';
import { Link } from 'react-router-dom';
import FaHome from '@meronex/icons/fa/FaHome';
import FaAddressBook from '@meronex/icons/fa/FaAddressBook';
import FaArrowRight from '@meronex/icons/fa/FaArrowRight';
import FaFileInvoice from '@meronex/icons/fa/FaFileInvoice';

const IconWripper = ({ children }) => {
  return (
    <div className="text-white text-5xl flex justify-center mb-5">
      {children}
    </div>
  );
};

const menus = [
  {
    label: 'Beranda',
    icon: (
      <IconWripper>
        <FaHome />
      </IconWripper>
    ),
    url: '/'
  },
  {
    label: 'Address',
    icon: (
      <IconWripper>
        <FaAddressBook />
      </IconWripper>
    ),
    url: '/alamat-pengiriman'
  },
  {
    label: 'Order',
    icon: (
      <IconWripper>
        <FaFileInvoice />
      </IconWripper>
    ),
    url: '/order'
  },
  {
    label: 'Logout',
    icon: (
      <IconWripper>
        <FaArrowRight />
      </IconWripper>
    ),
    url: '/logout'
  }
];

export default function UserAccount() {
  return (
    <LayoutOne>
      <TopBar />
      <Text as="h3">Akun</Text>
      <br />
      <Responsive desktop={4} tablet={4} mobile={2}>
        {menus.map((menu, index) => {
          return (
            <div className="px-2 pb-2" key={index}>
              <Link to={menu.url}>
                <Card
                  header={menu.icon}
                  body={
                    <div className="text-center font-bold text-white">
                      {menu.label}
                    </div>
                  }
                />
              </Link>
            </div>
          );
        })}
      </Responsive>
    </LayoutOne>
  );
}
