//todo 1. import module redux
import { createStore, combineReducers, compose, applyMiddleware } from 'redux';
//todo 1.1 import auth reducer
import authReducer from '../features/Auth/reducer';
import productReducer from '../features/Products/reducer';
import cartReducer from '../features/Cart/reducer';

//todo 2. import redux thunk
import thunk from 'redux-thunk';

//todo 3. buat composer enhancer untuk menghubungkan dengan Chrome DevTools Redux
const composerEnhancer = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

//todo 4. gabung reducer, untuk sementara kosong, karena kita belum membuat reducer
const rootReducer = combineReducers({
  auth: authReducer,
  products: productReducer,
  cart: cartReducer
});

//todo 5. buat store, dan gunakan composerEnhancer + middleware thunk
const store = createStore(
  rootReducer,
  composerEnhancer(applyMiddleware(thunk))
);

//todo 6. export store
export default store;
