//todo 1. import axios
import axios from 'axios';

//todo 2. import config
import { config } from '../config';

//todo 3. buat fungsi pendaftaran user baru
export async function registerUser(data) {
  return await axios.post(`${config.api_host}/auth/register`, data);
}

//todo 4. buat fungsi login
export async function login(email, password) {
  return await axios.post(`${config.api_host}/auth/login`, { email, password });
}

//todo 5. buat fungsi logout
export async function logout() {
  //* kirim token supaya server bisa kenal siapa sih user yang minta logout, token dibaca langsung dari localStorage
  let { token } = localStorage.getItem('auth')
    ? //* jika token ketemu parsing dari string ke object
      JSON.parse(localStorage.getItem('auth'))
    : {};

  return await axios
    .post(`${config.api_host}/auth/logout`, null, {
      //* gunakan token tersebut pada headers authorization sebagai Bearer token
      headers: { authorization: `Bearer ${token}` }
    })
    //* jika response sukses diterima dari server
    .then((response) => {
      //* maka hapus item auth dilocalStorage
      localStorage.removeItem('auth');
      return response;
    });
}
