import * as React from 'react';
import {
  LayoutOne,
  Card,
  FormControl,
  InputText,
  InputPassword,
  Button
} from 'upkit';
import { useForm } from 'react-hook-form';
import { useHistory, Link } from 'react-router-dom';

import StoreLogo from '../../components/StoreLogo';
import { useDispatch } from 'react-redux';
import { userLogin } from '../../features/Auth/actions';
import { rules } from './validation';
import { login } from '../../api/auth';

const statuslist = {
  idle: 'idle',
  process: 'process',
  success: 'success',
  error: 'error'
};

export default function Login() {
  const { register, handleSubmit, errors, setError } = useForm();
  const [status, setStatus] = React.useState(statuslist.idle);
  const dispatch = useDispatch();
  const history = useHistory();

  //todo 1. fungsi untuk menangani form submit
  const onSubmit = async ({ email, password }) => {
    //todo 2. set status menjadi 'process'
    setStatus(statuslist.process);

    //todo 3. kirim data ke web api menggunakan helper 'login'
    let { data } = await login(email, password);

    //todo 4. cek apakah server mengembalikan error
    if (data.error) {
      //todo 5. tangani error bertipe 'invalidCredential'
      setError('password', {
        type: 'inavlidCredential',
        message: data.message
      });
      //todo 6. set status menjadi 'error'
      setStatus(statuslist.error);
    } else {
      //todo 7. BERHASIL LOGIN
      //todo 8. ambil data 'user' & 'token' dari respon server
      let { user, token } = data;

      //todo 9. dispatch ke Redux store, action 'userLogin' dengan data 'user' & 'token'
      dispatch(userLogin(user, token));

      //todo 10. redirect ke halaman home
      history.push('/');
    }
    setStatus(statuslist.success);
  };
  return (
    <LayoutOne size="small">
      <br />
      <Card color="white">
        <div className="text-center mb-5">
          <StoreLogo />
        </div>
        <form onSubmit={handleSubmit(onSubmit)}>
          <FormControl errorMessage={errors.email?.message}>
            <InputText
              placeholder="Email"
              fitContainer
              name="email"
              ref={register(rules.email)}
            />
          </FormControl>
          <FormControl errorMessage={errors.password?.message}>
            <InputPassword
              placeholder="Password"
              fitContainer
              name="password"
              ref={register(rules.password)}
            />
          </FormControl>
          <Button fitContainer size="large" disabled={status === 'process'}>
            Login
          </Button>
        </form>
        <div className="text-center mt-2">
          Belum punya akun ?
          <Link to="/register">
            <b>Daftar Sekarang</b>
          </Link>
        </div>
      </Card>
    </LayoutOne>
  );
}
